.resultsContainer {
  @apply flex flex-col w-full gap-3;

  .chapterTitle {
    @apply flex justify-between items-center gap-3 text-wrap text-left;
  }

  .accordionContainer {
    @apply flex flex-col gap-3;

    .article {
      @apply w-full p-5 flex flex-col gap-3 items-start border border-gray-700 rounded-lg;

      .articleTitle {
        @apply text-lg font-bold;
      }

      .tableContainer {
        @apply w-full overflow-y-auto;

        .table {
          @apply w-full text-sm text-left rtl:text-right text-gray-400 rounded-lg overflow-hidden;

          .tableHead {
            @apply text-xs uppercase bg-gray-700 text-gray-400;

            .tableHeadItem {
              @apply px-6 py-3;
            }
          }

          .tableBody {
            .tableRow {
              @apply bg-gray-800 border-gray-700 hover:bg-gray-600;

              .tableRowItem {
                @apply px-6 py-4;

                .button {
                  @apply font-bold text-blue-500 hover:underline;
                }
              }
            }
          }
        }
      }
    }

    .questionsContainer {
      @apply flex flex-col gap-6 p-4 border border-gray-700 rounded-lg;

      .questionContainer {
        @apply flex flex-col gap-2;

        .correctIco {
          @apply text-green-400;
        }

        .incorrectIco {
          @apply text-red-400;
        }

        .questionTitle {
          @apply text-lg;
        }
      }
    }
  }

  @media (max-width: 768px) {
    .accordionContainer {
      .articleTitle {
        @apply p-3;
      }

      .questionsContainer {
        @apply p-3;
      }
    }
  }

  @media (max-width: 480px) {
    .accordionContainer {
      .articleTitle {
        @apply p-2 text-base;
      }

      .questionsContainer {
        @apply p-2;
      }

      .questionContainer {
        .questionTitle {
          @apply text-base;
        }
      }
    }
  }
}
